/**
 * The object below defines the navigation for the static website.
 * Single undercores represent spaces in nav item titles.
 * Double underscores represent dropdown nav items.
 */

const navItems = {
    Home: "/",
    Meet_the_Candidates__Trevor_Wowk: "/meet-the-candidates/trevor-wowk",
    Meet_the_Candidates__John_Doe: "/meet-the-candidates/john-doe",
}

/**
 * Export
 */
export default Object.entries(navItems).reduce((p, [rawNavItem, link]) => {
    const [navItem, subNavItem] = rawNavItem.split('__');
    const ni = navItem.replace(/_/g, ' ')

    if (!subNavItem) {
        return {
            ...p,
            [ni]: link,
        };
    }

    const sni = subNavItem.replace(/_/g, ' ')
    return {
        ...p,
        [ni]: {
            ...p[ni],
            [sni]: link,
        },
    };
}, {});
