import React from 'react'
import styles from './Footer.module.css'
import { graphql, useStaticQuery } from 'gatsby'

const Footer = () => {
    const data = useStaticQuery(graphql`
    query {
        allMarkdownRemark(filter: {fields: {slug: {regex: "/footer/"}}}) {
            nodes {
            id
            html
            }
        }
      }
    `)

    const footerHtml = data.allMarkdownRemark.nodes[0].html;

    return (
        <div className={styles.Footer} dangerouslySetInnerHTML={{ __html: footerHtml }} />
    )
}

export default Footer;