import { Link, useStaticQuery, graphql } from "gatsby"
import React, { useState } from "react"
import classNames from 'classnames'
import styles from "./Header.module.css"
import navItems from "../../content-management/navigation/navigation"
import { useScrollPosition } from "@n8tb1t/use-scroll-position"

const Header = ({ siteTitle, floatingHeader }) => {
    const [isSticky, setIsSticky] = useState(false)
    const [animating, setAnimating] = useState(false)

    useScrollPosition(() => {
        if (animating) {
            return
        }
        const offset = window.pageYOffset;
        const isMobile = window.innerWidth < 600

        let sticky = offset > 200 && !isMobile

        if (sticky != isSticky) {
            setAnimating(true)
            setTimeout(() => {
                setAnimating(false)
            }, 600)
        }

        setIsSticky(sticky)
    })

    const data = useStaticQuery(graphql`
        query {
            allFile(filter: {
                sourceInstanceName: { eq: "src" }
                relativePath: { eq: "content-management/images/general/nav-logo.png" }
            }) {
                edges {
                    node {
                    childImageSharp {
                        fluid {
                        ...GatsbyImageSharpFluid
                        }
                    }
                    }
                }
            }
        }
    `)

    const logoSrc = data.allFile.edges[0].node.childImageSharp.fluid.src;
    const className = classNames(styles.Header, isSticky && styles.Sticky, floatingHeader && styles.Floating)

    return (
        <>
            <header className={className}>
                <Link
                    to="/"
                    style={{
                        color: `white`,
                        textDecoration: `none`,
                        height: '100%',
                    }}
                    >
                    <img src={logoSrc} alt={siteTitle} className={styles.Logo} />
                </Link>
            </header>
            <div className={styles.StickyPlaceholder}></div>
        </>
    )
}

export default Header